#navbar {
    background-color: var(--color-gray-900);
    display: flex;
    flex-direction: column;
}

#logo {
    background: var(--logo-horizontal-inverse) no-repeat;
    background-size: contain;
    display: inline-block;
    height: 70px;
    width: 130px;
    margin: var(--size-sm);
}

#links {
    flex: auto;
}

#links .node{
    padding: var(--size-sm);
    color: var(--color-white);
}

#links .node a{
    color: var(--color-white);
}

#links .node.selected{
background-color: var(--color-gray-800);
border-inline-start:2px solid var(--color-orange-500);
}

#navbar footer {
    color: var(--color-white);
    font: var(--font-xs);
    padding: var(--size-sm);
}

#navbar footer a{
    color: var(--color-white);
    margin-inline: var(--size-xs);
}