#user-details {
    background-color: var(--color-gray-200);
    display: flex;
    flex-direction: column;
    padding-inline: var(--size-sm);
}

header {
    padding-block: var(--size-sm);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#btn-notifications {
    background-color: transparent;
    border: none;
}

#btn-notifications svg {
    fill: var(--color-blue-500);
    width: var(--size-md);
    height: var(--size-md);
}

#btn-notifications:hover svg {
    fill: var(--color-blue-700);
    transition: fill 0.2s;
}

#btn-infocard {
    background-color: var(--color-gray-700);
    border: none;
    border-radius: 50%;
    padding: var(--size-xs);
    box-sizing: content-box;
}

#btn-infocard:hover {
    background-color: var(--color-gray-900);
    transition: background-color 0.2s;
}

#btn-infocard svg {
    fill: var(--color-gray-100);
    width: var(--size-sm);
    height: var(--size-sm);
}

ul {
    position: absolute;
    inset-inline-end: var(--size-lg);
    margin-block-start: 0;
    list-style: none;
    padding: 0;
    background-color: var(--color-white);
    box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.2);
}

ul li {
    padding: var(--size-sm);
}

ul li svg {
    margin-inline-end: var(--size-xs);
}

ul li:nth-child(1) {
    border-bottom: 1px solid var(--color-gray-500);
    text-align: center;
    padding: var(--size-sm) var(--size-xl);
}

ul .title {
    font: var(--font-lg);
    display: block;
}


ul .subtitle {
    font: var(--font-sm);
    color: var(--color-gray-300);
    display: block;
}

#info {
    text-align: center;
    color: var(--color-gray-700);
    padding-bottom: var(--size-md);
}

#info>p {
    margin-block: var(--size-xs);
}

#info>p:nth-child(even) {
    color: var(--color-gray-600);
}

#customer-name {
    font: var(--font-xxl);
    margin-block: var(--size-xs);
}


#customer-service {
    border-top: 1px solid var(--color-gray-500);
    font: var(--font-sm);
    color: var(--color-gray-600);
    text-align: center;
    flex: auto;
    padding-top: var(--size-md);

}

#customer-service p {
    margin-block: var(--size-xs);
}

#user-details footer {
    display: flex;
    justify-content: space-around;
    padding-block: var(--size-sm);
}